@media (min-width: 768px) {
  .form-submission {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto;
  }
}
@media (min-width: 1440px) {
  .form-submission {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    gap: 80px;
