}

html {
  height: 100%;
  overflow-x: hidden;
}

body {
  position: relative;
  display: flex;
  min-height: 100%;
  margin: 0;
  flex-direction: column;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
  overflow-x: hidden;
}

.additional-card {
  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-template-rows: repeat(3, auto);
}
@media (min-width: 768px) {
  .additional-card {
    grid-template-rows: repeat(2, auto);
  }
}
@media (min-width: 1440px) {
  .additional-card {
    grid-template-columns: 1fr 1fr 245px;
    grid-template-rows: 1fr;
  }
}

.additional-card__link {
  text-decoration: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

.additional-card__title {
  margin: 0 0 13px;
  grid-column: 1/2;
  grid-row: 1/2;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}
@media (min-width: 768px) {
  .additional-card__title {
    grid-column: 1/2;
    grid-row: 2/3;
    font-size: 20px;
    line-height: 20px;
    margin: 0 0 24px;
  }
}
@media (min-width: 1440px) {
  .additional-card__title {
    grid-column: 1/2;
    grid-row: 1/2;
    margin: 0;
  }
}

.additional-card__list {
  grid-column: 1/3;
  grid-row: 2/3;
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: auto;
  width: 100%;
  font-family: "Arial", sans-serif;
  line-height: 14px;
  margin: 0 0 12px;
}
@media (min-width: 768px) {
