}

.additional-card__term {
  grid-column: 2/3;
  grid-row: 1/2;
  text-align: right;
}
@media (min-width: 768px) {
  .additional-card__term {
    grid-column: 1/2;
    grid-row: 1/-1;
    place-self: start end;
    margin-right: 15px;
